import React from 'react'
import {Link, useStaticQuery} from 'gatsby'
import Img from 'gatsby-image'

import './styles/productssection.scss'

function Productssection({menuClicked}){

  const data = useStaticQuery(graphql`
  query{
    allFile(filter:{relativeDirectory:{eq:"product-images"}}){
      edges{
        node{
          name
          childImageSharp{
            fixed(width:300){
              ...GatsbyImageSharpFixed
            }
          }
        }
      }
    }
  }
  `)

  data.allFile.edges.sort((a, b) => Number(a.node.name) - Number(b.node.name))

  return(
    <section className={`products-section ${menuClicked && 'mobile-menu-active'}`}>
      <div className='product'>
        <Img fixed={data.allFile.edges[0].node.childImageSharp.fixed}/>
        <h2>Basic</h2>
        <p>Podstawowy wariant urzdzenia z obsug manualn. Aplikator uruchamiany jest poprzez nacisk stop, co pozwala ograniczy konieczno dotykania przycisku domi. Model Basic to wersja analogiczna, ktra posiada wszystkie podstawowe funkcjonalnoci produktw premium. Pozbawiona jest jednake moliwoci zdalnego kontrolowania stanu poprzez aplikacj i wymaga samodzielnej weryfikacji pojemnikw z pynem dezynfekujcym</p>
        <p>Aplikator uatwia zachowanie higieny wszystkim osobom obecnym na terenie obiektu lub zakadu. Wystarczy skorzysta z urzdzenia w dowolnym momencie, woy rce pod aplikator, a nastpnie wcisn przycisk nog. Rozpylony pyn dezynfekujcy po zaledwie kilku sekundach eliminuje do 85% drobnoustrojw z powierzchni rk. Urzdzenie wymienne wkady z pynem, ktre mona zamwi bezporednio od nas.</p>
        <div className='minor-info'>
          <p>Szybka dezynfekcja,</p>
          <p>Ekonomiczny wkad o duej pojemnoci,</p>
          <p>Moliwo montau zewntrznego,</p>
          <p>Wytrzymaa konstrukcja.</p>
        </div>
        <Link to='#'>Czytaj wicej...</Link>
      </div>   
      <div className='product'>
      <Img fixed={data.allFile.edges[1].node.childImageSharp.fixed}/>
        <h2>Smart</h2>
        <p>Inteligentny wariant urzdzenia z moliwoci zdalnego zarzdzania. Aplikator wyposaony jest w zblieniowy dozownik, ktry spryskuje donie automatycznie po zblieniu ich do czujnika. Ogranicza to konieczno bezporedniego kontaktu z korpusem urzdzenia i znacznie podnosi higien na terenie kadego obiektu lub zakadu.</p>
        <p>Smart moe by obsugiwany zdalnie i nie wymaga samodzielnego sprawdzania stanu pojemnikw z pynem. Z poziomu smartfona otrzymujesz dostp do wszystkich informacji na temat kadego urzdzenia. Dedykowana aplikacja pozwala na zarzdzanie kadym aplikatorem z osobna, z dowolnego miejsca. Opcja smart umoliwia zdalny podgld statusu urzdzenia, a take stanu baterii w akumulatorze i wkadu z pynem. Aplikacja posiada rozbudowany system powiadomie, a take pozwala na zamawianie wkadw z poziomu menu gwnego.</p>
        <div className='minor-info'>
          <p>Zoptymalizowana obsuga urzdzenia,</p>
          <p>Ekonomiczne zarzdzanie pynem,</p>
          <p>Wkad o duej pojemnoci z inteligentnym dozownikiem, </p>
          <p>Dedykowana aplikacja dla kadego aplikatora, </p>
          <p>Wbudowany akumulator,</p>
          <p>Moliwo zewntrznego i wewntrznego montau, </p>
          <p>Bezdotykowy system aplikacji.</p>
        </div>
        <Link to='#'>Czytaj wicej...</Link>
      </div>  
      <div className='product'>
      <Img fixed={data.allFile.edges[2].node.childImageSharp.fixed}/>
        <h2>Smart+</h2>
        <p>Ulepszony wariant podstawowego moduu Smart. Posiada dodatkowe miejsca na reklam. Urzdzenie ma wszystkie atuty, ktrymi wyrnia si Smart, jednake optymalizuje kady z nich. Umoliwia rwnie efektywne promowanie wasnych produktw i usug. Aplikator ustawiony na zewntrz pozwala przedstawi potencjalnym klientom moliwoci, ktre mog uzyska poprzez wspprac z firm.</p>
        <p>Aplikator posiada dozownik zblieniowy, ktry spryskuje donie automatycznie po zblieniu ich do fotokomrki. Wyklucza to konieczno wciskania przycisku lub stycznoci korpusem urzdzenia. Ograniczenie kontaktu fizycznego podnosi znacznie higien na terenie kadego obiektu lub zakadu.</p>
        <p>Model Smart+ moe by zarzdzany praktycznie w peni zdalnie. Nie wymaga on samodzielnego sprawdzania stanu pojemnikw z pynem, gdy z poziomu aplikacja otrzymujesz dostp do kadego urzdzenia. Dedykowane oprogramowanie pozwala na podgld wszystkich informacji i zarzdzanie kadym aplikatorem z osobna. Aplikacja posiada rozbudowany system powiadomie, a take pozwala na zamawianie wkadw z poziomu menu gwnego. Opcja Smart+ umoliwia zdalny podgld statusu urzdzenia, stanu jego akumulatora i wkadu z pynem. </p>
        <div className='minor-info'>
          <p>Dodatkowe miejsce na reklam, </p>
          <p>Ekonomiczne zarzdzanie pynem,</p>
          <p>Zoptymalizowana obsuga urzdzenia,</p>
          <p>Dedykowana aplikacja dla kadego aplikatora,</p>
          <p>Wbudowany akumulator,</p>
          <p>Moliwo zewntrznego i wewntrznego montau,</p>
          <p>Wkad o duej pojemnoci z inteligentnym dozownikiem,</p>
          <p>Bezdotykowy system aplikacji.</p>
        </div>
        <Link to='#'>Czytaj wicej...</Link>
      </div>   
      <div className='product'>
      <Img fixed={data.allFile.edges[3].node.childImageSharp.fixed}/>
        <h2>Premium</h2>
        <p>Wariant aplikatorw Premium oferuje kademu z klientw najwyszy standard higieny. Stanowi on efekt lat praktyki oraz poczenie moliwoci wszystkich aplikatorw dostpnych w naszej ofercie. Premium posiada nie tylko kad z funkcjonalnoci modeli Smart, ale take rozwija kad z nich, ze szczeglnym naciskiem nastawionym na ekonomi oraz systemy oszczdnego dozowania pynu. Ponadto wyposaony jest w modu z dwustronnym monitorem na reklamy, ktre mona zmienia z  bezporednio z poziomu aplikacji. Urzdzenie posiada efektowne wykoczenie bokw niebieskim podwietleniem typu LED.</p>
        <p>Aplikator posiada ulepszony dozownik zblieniowy, ktry wyklucza konieczno dotykania przycisku lub korpusu urzdzenia. Spryskuje on automatycznie donie po zblieniu ich do czujnika. Wykluczenie kontaktu fizycznego wpywa definitywnie na higien na terenie caego obiektu. Premium wyposaony jest w szczelny korpus, ktry pozwala na ustawienie go na zewntrz budynku.</p>
        <p>Model Premium moe by zarzdzany praktycznie w peni zdalnie. Wyjtek stanowi konieczno wymiany pojemnikw z pynem do dezynfekcji. Aplikator posiada jednak innowacyjny system dozowania, ktry ogranicza zuycie pynu do efektywnego minimum. Kada porcja naniesiona na donie zabija 100% drobnoustrojw przy zachowaniu niskiego uycia wkadu. </p>
        <p>Premium moe by obsugiwany wycznie z poziomu aplikacji. Posiada ona rozbudowany system powiadomie, a take pozwala na zamawianie wkadw menu gwnym. Nie wymaga samodzielnego sprawdzania stanu pojemnikw z pynem lub poziomu baterii. Z ekranu smartfona otrzymujesz dostp do kadego urzdzenia. Dedykowane oprogramowanie pozwala na podgld wszystkich informacji i zarzdzanie kadym aplikatorem z osobna. Opcja Smart+ pozwala na zdalny podgld statusu urzdzenia, stanu jego akumulatora i wkadu z pynem. </p>
        <div className='minor-info'>
          <p>Wkad o duej pojemnoci z inteligentnym dozownikiem,</p>
          <p>Zoptymalizowana obsuga urzdzenia,</p>
          <p>Wzmocniony korpus odporny na czynniki atmosferyczne,</p>
          <p>Moliwo zewntrznego i wewntrznego montau,</p>
          <p>Dedykowana aplikacja dla kadego aplikatora,</p>
          <p>Zdalne sterowanie wywietlaniem reklam,</p>
          <p>Dodatkowe miejsce na reklam,</p>
          <p>Ekonomiczne zarzdzanie pynem,</p>
          <p>Wbudowany akumulator,</p>
          <p>Bezdotykowy system aplikacji. </p>
        </div>
        <Link to='#'>Czytaj wicej...</Link>
      </div>     
    </section>
  )
}

export default Productssection