import React from 'react'
import Swiper from 'react-id-swiper'

import 'swiper/css/swiper.css'
import './styles/section.scss'
import './styles/ourmissionsection.scss'

function Ourmissionsection({menuClicked}){

  return(
    <section className={`ourmission-section ${menuClicked && 'mobile-menu-active'}`}>
      <div>
        <h2>Nasza misja</h2>
          <div className='testimonial'>
            <q>
            Od lat zajmujemy si promowaniem utrzymywania higieny w przestrzeni publicznej, a take na terenie zakadw pracy. Dlaczego? Z tego wzgldu, e tylko i wycznie profilaktyka stanowi skuteczn form przeciwdziaania ponad 80% bakterii i wirusw, szczeglnie osonkowych. Dbamy o zdrowie i samopoczucie nie tylko naszych klientw, ale rwnie ich najbliszych oraz pracownikw.<br/>
            Wieloletnia praktyka wskazuje, e kluczem do zachowania wysokiego standardu czystoci na terenie kadego miejsca zatrudnienia, jest instalacja aplikatora pynu dezynfekujcego. Zaledwie jedno urzdzenie pozwala ograniczy o poow zachorowalno wrd 
            pracownikw. Dlaczego? Z tego wzgldu, e sama jego obecno wyrabia nawyk korzystania. Dezynfekcja doni wyklucza bowiem moliwo kontaktu bakterii na doniach z tkank nabonka. Odpowiednia higiena, to dodatkowa optymalizacja zarzdzania, ktra prowadzi bezporednio do wzrostu efektywnoci i obrotw na terenie kadej firmy. 
            </q>
          </div>
      </div>
    </section>
  )
}

export default Ourmissionsection