import React from 'react'
import {graphql, useStaticQuery} from 'gatsby'
import Img from 'gatsby-image'

import './styles/standoutsection.scss'

function Standoutsection(){

  const data = useStaticQuery(graphql`
    query{
      file(relativePath:{eq:"standout-images/test.jpg"}){
        childImageSharp{
          fluid(maxWidth:1920){
            ...GatsbyImageSharpFluid_withWebp
          }
        }
      }
    }
  `)

  return(
    <section className='standout-section'>
      <div>
        <h2>Dostosuj kade z urzdze do swoich potrzeb</h2>
        <p>Zapewniamy moliwo dowolnej customizacji aplikatorw do indywidualnych preferencji klienta. Moemy dowolnie dostosowywa sprzt do Twojej wizji. Kady z aplikatorw moe zosta wykonany z logo i w kolorystyce Twojej firmy. Wystarczy, e skontaktujesz si z nami i przedstawisz nam swj pomys. </p>
        <p>Dopasujemy Twoj koncepcj do naszego sprztu. Dziki temu moesz zakupi zestaw aplikatorw, ktre bd dba o higien pracownikw, a zarazem promowa Twoje produkty lub usugi. </p>
        <Img fluid={data.file.childImageSharp.fluid} />
      </div>
    </section>
  )
}

export default Standoutsection