import React from 'react'
import {Link, useStaticQuery, graphql} from 'gatsby'
import Img from 'gatsby-image'

import './styles/detailssection.scss'

function Detailssection(){

  const data = useStaticQuery(graphql`
  query {
    rowMainImages:allFile(filter: {relativeDirectory:{eq: "details-images"}}){
      edges{
        node{
          name
          childImageSharp{
            fluid(maxWidth:960){
              ...GatsbyImageSharpFluid_withWebp
            }
          }
        }
      }
    }
    benefitsIcons:allFile(filter: {relativeDirectory:{eq:"details-images/second-row-icons"}}){
      edges{
        node{
          name
          childImageSharp{
            fixed(width:44){
              ...GatsbyImageSharpFixed
            }
          }
        }
      }
    }
  }
  `)

  data.rowMainImages.edges.sort((a, b) => Number(a.node.name) - Number(b.node.name))

  return(
    <section className='details-section'>
      <div className='details-item'>
        <div className='details-image'>
          <Img fluid={data.rowMainImages.edges[0].node.childImageSharp.fluid} imgStyle={{objectFit:"cover"}}/>
        </div>
        <div className='details-text'>
          <h2>Zarzdzaj kadym aplikatorem z poziomu aplikacji mobilnej!</h2>
          <ul>
            <li>
            Wikszo urzdze dostpnych w naszej ofercie posiada moliwo obsugi zdalnej. Powoduje to, e kadym z osobna mona zarzdza z dowolnego miejsca. Zapewnia to niezwyk oszczdno czasu, gdy wszystko moesz zrobi nie wstajc z fotela. Kiedy chcesz moesz sprawdzi stan zapenienia pojemnika oraz naadowania baterii.
            </li>
            <li>
            Ponadto nie musisz martwi si zakupem kolejnych wkadw. Wystarczy, e klikniesz odpowiedni przycisk z poziomu menu gwnego. Centrala otrzymuje zgoszenie i cigu 1-2 dni, wkad zostaje dostarczony kurierem.
            </li>
            <li>
            Niezbdne jest jedynie wasnorczne montowanie nowych wkadw. Nasze dozowniki wyliczaj w tym kontekcie idealn proporcj pynu, ktry zabija do 100% bakterii przy jak najwikszej oszczdnoci rodka dezynfekujcego. W efekcie wymiana w zalenoci od wielkoci Twojej firmy bdzie konieczna czciej ni raz na co najmniej miesic.
            </li>
            <li>
            Aplikacja pozwala zarzdza kadym urzdzeniem, ktre zakupisz. W gwnym panelu znajduje si si lista aplikatorw cznie z nazw modelu, jego lokalizacj, stanem baterii  oraz procentowym napenianieniem wkadu. Moesz kontrolowa kade z urzdze z osobna i sprawdza jego dokadne statystyki. System posiada rozbudowan funkcj powiadomie, ktry poinformuje Ci m.in.  o koniecznoci napenienia wkadu, jeli jego stan zejdzie poniej 10%.
            </li>
          </ul>
          <p className='bold centered'>Coming Soon</p>
        </div>
      </div>
      <div className='details-item'>
        <div className='details-text'>
          <h2>Komfort i higiena na lata</h2>
          <ul>
            <li>
            Nasze urzdzenia to w peni samoobsugowe, bezdotykowe aplikatory do dezynfekcji rk. Praktycznie kade wyposaone jest w czujnik na podczerwie, ktry po zblieniu doni automatycznie spryskuje ich powierzchni rodkiem dezynfekujcym. Obsuga bezdotykowa podnosi znacznie poziom higieny oraz nie pozwala rozwija si drobnoustrojom na powierzchni urzdzenia. 
            </li>
            <li>
            Kady aplikator moe by montowany zarwno na zewntrz, jak i wewntrz. Modele zdalne posiadaj wbudowan bateri, ktra umoliwia monitorowanie ich stanu w dowolnej chwili. Ponadto z poziomu aplikacji mona jednym klikniciem zamwi wkad, ktry w cigu 1-2 dni wysyany jest kurierem. 
            </li>
            <li>
            Urzdzenia z naszej oferty posiadaj inteligentny system dozowania pynu. Nasze wkady wystarczaj nawet na kilka miesicy bez potrzeby wymiany, w zalenoci od wielkoci Twojej firmy lub obiektu. Kady aplikator precyzyjnie dozuje ilo pynu do wielkoci doni, przy zachowaniu jak najwikszej oszczdnoci zuycia. Moesz mie pewnoci, e usuwa do 100% bakterii i wirusw osonkowych.  
            </li>
            <li>
            Kady z aplikatorw posiada wytrzymay korpus, ktry osania komponenty wewntrzne przed czynnikami atmosferycznymi i uszkodzeniami mechanicznymi. Proponowany przez nasz firm bezdotykowy punkt dezynfekcji charakteryzuje si wysok jakoci wykonania i atrakcyjnym wygldem. Nasze urzdzenia to inwestycja na lata. 
            </li>
          </ul>
          <div className='centered'>
            <Link to='#'>
              Get yours
            </Link>
          </div>
        </div>
        <div className='details-image'>
          <Img fluid={data.rowMainImages.edges[1].node.childImageSharp.fluid} />
        </div>
      </div>
      <div className='details-item'>
        <div className='details-image'>
         <Img fluid={data.rowMainImages.edges[2].node.childImageSharp.fluid} />
        </div>
        <div className='details-text'>
          <div
            style={{
              display:'flex',
              flexDirection:'column',
              justifyContent:'center',
              height:'100%'
            }}
          >
            <h2>Ekonomiczne wkady i kasety z pynem </h2>
            <p>Kade z naszych urzdze zostao wyposaone w inteligentny system oszczdnego dozowania pynu. Zapewniamy bardzo niskie zuycie przy zachowaniu efektywnoci w usuwaniu bakterii. Jeli stan pojemnika zejdzie poniej minimum, zostaniesz o Tym powiadomiony z poziomu aplikacji mobilnej. Wystarczy jedno kliknicie, aby zamwi zapasowy wkad.  </p>
            <p>Wymiana jest prosta i zajmuje kilka chwil. Wymaga to zaledwie czterech czynnoci, otwarcia urzdzenia, wycignicia zuytego wkadu, woenia nowego i zamknicia.</p>
            <div className='centered'>
              <Link to='#'>
                Shop Now
              </Link>
            </div>
          </div>
        </div>
      </div>
      
    </section>
  )
}

export default Detailssection