import React from 'react'
import {useStaticQuery, graphql} from 'gatsby'
import Img from 'gatsby-image'

import './styles/section.scss'
import './styles/benefitssection.scss'

const benefitsArr = [
  {
    img:'https://via.placeholder.com/44',
    heading:"Smart aplikacja",
    paragraph:'Zapewniamy moliwo zdalnego zarzdzania kadym aplikatorem. Aplikacja pozwala na podgld statusu urzdzenia, stanu baterii/wkadu z pynem.'
  },{
    img:'https://via.placeholder.com/44',
    heading:"Intuicyjne i bezdotykowe ",
    paragraph:'Urzdzenia dziaaj w peni automatycznie. Wystarczy zbliy donie, a aplikator samodzielnie spryska je pynem dezynfekujcym.'
  },{
    img:'https://via.placeholder.com/44',
    heading:"Oszczdno rodka dezynfekujcego",
    paragraph:'Jeden wkad wystarcza na utrzymanie higieny w caej firmie przez kilka miesicy. '
  },{
    img:'https://via.placeholder.com/44',
    heading:"Trwaa i solidna konstrukcja ",
    paragraph:'Wszystkie urzdzenia wykonane z solidnych korpusw metalowych. Produkty przeznaczone s do uytku wewntrznego i zewntrznego.'
  },{
    img:'https://via.placeholder.com/44',
    heading:"Bezobsugowo",
    paragraph:'Obsuga i konserwacja urzdzenia nie wymaga specjalistycznej wiedzy. Wystarczy wymieni pyn, o czym informuje wczeniej aplikacja.'
  },{
    img:'https://via.placeholder.com/44',
    heading:"Produkt polski",
    paragraph:'Aplikator zosta zaprojektowany i jest produkowany wycznie na terenie Polski, tak jak wikszo komponentw oraz materiaw niezbdna do jego produkcji.'
  },
]

function Benefitssection({menuClicked}){

  const data = useStaticQuery(graphql`
  query MyQuery {
    allFile(filter: {relativeDirectory: {eq: "benefits-images"}}) {
      edges{
        node{
          name
          childImageSharp {
            fixed(width: 44) {
              ...GatsbyImageSharpFixed
            }
          }
        }
      }
    }
  }
  `)

  data.allFile.edges.sort((a, b) => Number(a.node.name) - Number(b.node.name))

  return(
    <section className={`benefits-section ${menuClicked && 'mobile-menu-active'}`}>
      <div>
        <h2>Czym wyrniaj si nasze produkty?</h2>
        <div
          className='benefits-group'
        >
         {benefitsArr.map((benefit, index) => <div key={index}>
           <Img fixed={data.allFile.edges[index].node.childImageSharp.fixed} alt={benefit.heading + ' icon'}/>
           <h6>{benefit.heading}</h6>
           <p>{benefit.paragraph}</p>
         </div>)}
        </div>
      </div>
    </section>
  )
}

export default Benefitssection